.mobile-menu {
  position: absolute;
  top: 25px;
  right: 18px;
  z-index: 3;
}
.menu-btn {
  position: relative;
  display: block;
  z-index: 999;
  width: 20px;
  height: 12px;
  background: none;
}

.menu-btn::before,
.menu-btn::after {
  content: '';
}
.menu-btn::before,
.menu-btn__line,
.menu-btn::after {
  width: 20px;
  height: 1px;
  stroke-width: 1px;
  background-color: $colorTextTitle;
  position: absolute;
  background-color: $colorTextTitle;
  left: 0;
  transition: 0.5s linear;
  transition-property: top, transform;
  z-index: 0;
}

.menu-btn::before {
  top: 0;
}
.menu-btn__line {
  top: calc(50% - 1px);
}
.menu-btn::after {
  top: calc(100% - 2px);
}

.menu-btn.is-open {
  &::before {
    top: calc(50% - 1px);
    transform: rotate(-45deg);
    background-color: #fff;
  }
  .menu-btn__line {
    transform-origin: top;
    transform: scaleX(0);
  }
  &::after {
    top: calc(50% - 1px);
    transform: rotate(45deg);
    background-color: #fff;
  }
}
@media (min-width: 768px) {
  .mobile-menu {
    margin-top: 28px;
    margin-right: 225px;
  }
}
@media (min-width: 1200px) {
  .mobile-menu {
    margin-top: 0;
    margin-right: 350px;
  }
  .menu-btn {
    display: none;
  }
}
