.mobile-menu {
  z-index: 3;
  position: absolute;
  top: 25px;
  right: 18px;
}

.menu-btn {
  z-index: 999;
  width: 20px;
  height: 12px;
  background: none;
  display: block;
  position: relative;
}

.menu-btn:before, .menu-btn:after {
  content: "";
}

.menu-btn:before, .menu-btn__line, .menu-btn:after {
  width: 20px;
  height: 1px;
  stroke-width: 1px;
  z-index: 0;
  background-color: #d41443;
  transition: top .5s linear, transform .5s linear;
  position: absolute;
  left: 0;
}

.menu-btn:before {
  top: 0;
}

.menu-btn__line {
  top: calc(50% - 1px);
}

.menu-btn:after {
  top: calc(100% - 2px);
}

.menu-btn.is-open:before {
  background-color: #fff;
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}

.menu-btn.is-open .menu-btn__line {
  transform-origin: top;
  transform: scaleX(0);
}

.menu-btn.is-open:after {
  background-color: #fff;
  top: calc(50% - 1px);
  transform: rotate(45deg);
}

